import React, { Component } from 'react'

export default class NumberNationalities extends Component {
    render() {
        //get the nacionality
        let nacionality = this.props.athleteList.map(athelete => {
            return athelete.athlete_athlete.nacionality;
        });
        //remove duplicates
        const Tableuniquenacionality = [...new Set(nacionality)];
        return (
            <div>
                {/* Info boxes */}
                <div className="col-md-3 col-sm-6 col-xs-12">
                    <div className="info-box">
                        <span className="info-box-icon bg-red"><i className="fa fa-fw fa-map-o" /></span>
                        <div className="info-box-content">
                            <span className="info-box-text">Nacionalidades</span>
                            <span className="info-box-number">{Tableuniquenacionality.length}</span>
                        </div>
                        {/* /.info-box-content */}
                    </div>
                    {/* /.info-box */}
                </div>

            </div>
        )
    }
}
