import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"

var size = window.innerWidth;


export default class TableSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }
  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }
  }

  render() {
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.escalao;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category)];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.country;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality)];
    //get the Local
    let local = this.props.athleteList.map(athelete => {
      return athelete.local;
    });
    //remove duplicates
    const Tableuniquelocal = [...new Set(local)];
    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club)];
    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: (size>800)?"Peitoral":"PTL",
        id: "peitoral",
        accessor: d => Number(d.peitoral),
        style: {
          textAlign: "Center"
        },
        width:(size>800)?100:90,
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Nome",
        accessor: "name",
        width:(size>800)?300:175,
        style: {
          textAlign: "Center"
        },
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Equipa",
        accessor: "club",
        show : this.CheckSize(),
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Escalão",
        accessor: "escalao",
        width:(size>800)?150:100,
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },

      {
        Header: "País",
        accessor: "country",
        width:150,
        show : this.CheckSize(),
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Local",
        accessor: "local",
        width:(size>800)?150:100,

        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquelocal.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquelocal.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },

      {
        Header: (size>800)?"Favorito":"FAV",
        width:(size>800)?100:50,

        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <Favorites ListaFavoritos={row.original.athlete_id} />

        )
      }
    ];
    var minsize = (parseInt(this.props.athleteList.length) > 100) ? parseInt(this.props.athleteList.length * 0.20) : parseInt(this.props.athleteList.length * 0.50);
    if (minsize === 0) {
      minsize = 2;
    }
    return (
      <ReactTable
        defaultSorted={[
          {
            id: "peitoral",
            desc: false
          }
        ]}
        previousText={"Anterior"}
        nextText={"Proximo"}
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={"Sem dados"}
        defaultPageSize={minsize}
        pageSizeOptions={(parseInt(this.props.athleteList.length) > 100) ? [parseInt(this.props.athleteList.length * 0.20), parseInt(this.props.athleteList.length * 0.40), parseInt(this.props.athleteList.length * 0.50), parseInt(this.props.athleteList.length * 0.70), parseInt(this.props.athleteList.length * 1)] : [parseInt(this.props.athleteList.length * 0.50), parseInt(this.props.athleteList.length * 1)]} showPagination={true}
        showPaginationTop={false}
        showPaginationBottom={true}
        showPageSizeOptions={true}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={true}
        multiSort={true}
        resizable={true}
        defaultSortDesc={false}
        className="-striped -highlight"

      />
    );
  }
}
