import React, { Component } from "react";
import ReactTable from "react-table";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"
import {FormatTime} from "../Scripts/time"

var size = window.innerWidth;

export default class TableSubscribers extends Component {

  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }
  }

  render() {
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.escalao;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category)];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.country;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality)];

    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club)];



    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: (size>800)?"Posição":"POS",
        accessor: "PositionGlobal",
        style: {
          textAlign: "Center"
        },
        width:(size>800)?100:55,
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        )
      },
      {
        Header: (size>800)?"Peitoral":"PTL",
        id: "peitoral",
        accessor: d => Number(d.peitoral),
        style: {
          textAlign: "Center"
        },
        width:(size>800)?100:60,
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (
          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Nome",
        accessor: "name",
        width:(size>800)?300:130,
        style: {
          textAlign: "Center"
        },
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar nome"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (
          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Equipa",
        accessor: "club",
        show: this.CheckSize(),
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Escalão",
        accessor: "escalao",
        width:(size>800)?150:85,
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "País",
        accessor: "country",
        width:150,
        style: {
          textAlign: "Center"
        },
        show: this.CheckSize(),
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Tempo",
        accessor: "acumulated_time",
        filterable: false,
        sortable: false,
        width:(size>800)?100:70,
        style: {
          textAlign: "Center"
        },
        Cell: row => FormatTime(row.value)

      },
      {
        Header: (size>800)?"Dif Primeiro":"Dif Pri",
        accessor: "dif",
        filterable: false,
        sortable: false,
        width:(size>800)?100:70,
        style: {
          textAlign: "Center"
        },
        Cell: row => FormatTime(row.value)
      },
      {
        Header: (size>800)?"Favorito":"FAV",
        filterable: false,
        sortable: false,
        width:(size>800)?100:50,
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <Favorites ListaFavoritos={row.original.athlete_id} />

        )
      }
    ];

    return (
      <ReactTable
        defaultSorted={[
          {
            id: "PositionGlobal",
            desc: false
          }
        ]}
        previousText={"Anterior"}
        nextText={"Proximo"}
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={"Sem dados"}
        defaultPageSize={(parseInt(this.props.athleteList.length) === 0) ? 15 : parseInt(this.props.athleteList.length * 0.20)}
        pageSizeOptions={[parseInt(this.props.athleteList.length * 0.20), parseInt(this.props.athleteList.length * 0.40), parseInt(this.props.athleteList.length * 0.50), parseInt(this.props.athleteList.length * 0.70), parseInt(this.props.athleteList.length * 1)]}
        showPagination={true}
        showPaginationTop={false}
        showPaginationBottom={true}
        showPageSizeOptions={true}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={true}
        multiSort={true}
        resizable={true}
        className="-striped -highlight"
      />
    );
  }
}
