import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"

var size = window.innerWidth;


export default class TableSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }

  CheckSize() {
    if(size > 800){
      return true;
    }
    else{
      return false;
    }
  }

  addFavorites(){
    localStorage.setItem("ListaFavoritos",this.props.ListaFavoritos)
}
  render() {
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.category;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category)];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.athlete_athlete.nacionality;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality)];
    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club)];
    //get the inscription_status_code
    let inscription_status_code = this.props.athleteList.map(athelete => {
      return athelete.inscription_status_code;
    });
    //remove duplicates
    const Tableuniqueinscription_status_code = [
      ...new Set(inscription_status_code)
    ];

    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_athlete.athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: "Peit",
        id: "dorsal_number",
        accessor: d => Number(d.dorsal_number),
        style: {
          textAlign: "Center"
        },
        width: 70,
        maxWidth: 70,
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar peitoral"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_athlete.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Nome",
        accessor: "athlete_athlete.name",
        style: {
          textAlign: "Center"
        },
        Filter: ({ filter, onChange }) => (
          <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder="Procurar nome"
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_athlete.athlete_id}` }}
          >
            <span style={{ color: "#605ca8" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: "Equipa",
        show : this.CheckSize(),

        accessor: "club",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlign: "Center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Escalão",
        accessor: "category",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlign: "Center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "País",
        show : this.CheckSize(),

        accessor: "athlete_athlete.nacionality",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: "Estado",
        accessor: "inscription_status_code",
        style: {
          textAlign: "Center"
        },
        Cell: row => {
          if (row.value === "FIN") {
            return "Terminou";
          } else if (row.value === "EP") {
            return "Em Prova";
          } else if (row.value === "DNS") {
            return "Não Partiu";
          } else if (row.value === "D") {
            return "Cancelado";
          } else if (row.value === "DNF") {
            return "Desistiu";
          } else if (row.value === "DSQ") {
            return "Desqualificado";
          }

        },
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueinscription_status_code.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            <option value="FIN">Terminou</option>
            <option value="EP">Em Prova</option>
            <option value="DNS">Não Partiu</option>
            <option value="D">Cancelado</option>
            <option value="DNF">Desistiu</option>
            <option value="DSQ">Desqualificado</option>
          </select>
        )
      },
      {
        Header: "Fav",
        filterable: false,
        sortable: false,
        width: 50,
        maxWidth: 50,
        style: {
          textAlign: "Center"
        },
        Cell: row => (

          
          <Favorites ListaFavoritos={row.original.athlete_athlete.athlete_id} />

        )
      }
    ];
    return (
      <ReactTable
        defaultSorted={[
          {
            id: "dorsal_number",
            desc: false
          }
        ]}
        previousText={"Anterior"}
        nextText={"Proximo"}
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={"Sem dados"}
        defaultPageSize={parseInt(this.props.athleteList.length * 0.20)}
        pageSizeOptions={[parseInt(this.props.athleteList.length * 0.20), parseInt(this.props.athleteList.length * 0.40), parseInt(this.props.athleteList.length * 0.50), parseInt(this.props.athleteList.length * 0.70), parseInt(this.props.athleteList.length * 1)]}
        showPagination={true}
        showPaginationTop={false}
        showPaginationBottom={true}
        showPageSizeOptions={true}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={true}
        multiSort={true}
        resizable={true}
        defaultSortDesc={false}
        className="-striped -highlight"

      />
    );
  }
}
