import React, { Component } from "react";
import { NavLink } from "react-router-dom";


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var size = window.innerWidth;
var menuSizeTop = 16;
var menuSizeItem = 14;

const style={fontWeight: "bold", color: "#605ca8"};
export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CompetitionList: [],
      EventInfo: [],
      loading: false,
    };
    if (!window.location.hash) {
      window.location = window.location + '#Loaded';
      window.location.reload();
    }
    this.getEventInfo();
    this.getCompetitionList();
    this.CheckSize();
  }

  CheckSize() {
    if (size < 800) {
      menuSizeItem = 21;
      menuSizeTop = 23;
    }
  }

  getCompetitionList = async () => {

    const API_CALL = await fetch(
      `${API_URL}/trail/Competitions/${this.props.eventID}/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    this.setState({ CompetitionList: data });

  };

  getEventInfo = async () => {

    const API_CALL = await fetch(
      `${API_URL}/trail/Event/${this.props.eventID}/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    this.setState({ EventInfo: data, loading: true });
  };




  render() {
    let menuStatic;
    if (this.state.loading === true) {
      if (this.props.competitionID === 0) {
        menuStatic = (
          <li className="treeview active">
            <a href="##">
              <i style={{ fontSize: `${menuSizeTop}px` }} className="fa  fa-road" />
              <span style={{ fontSize: `${menuSizeTop}px` }}>{(this.state.loading===true)?this.state.EventInfo.short_name:"Carregando..."}</span>
              <span className="pull-right-container">
                <i className="fa fa-angle-left pull-right" />
              </span>
            </a>
            <ul className="treeview-menu active">
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventParticipants/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Inscritos
                  </NavLink>
              </li>
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Favorites/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Favoritos
                </NavLink>
              </li>
            </ul>
          </li>
        );
      }
      else {
        menuStatic = (
          <li className="treeview">
            <a href="##">
              <i style={{ fontSize: `${menuSizeTop}px` }} className="fa  fa-road" />
              <span style={{ fontSize: `${menuSizeTop}px` }}>{(this.state.loading===true)?this.state.EventInfo.short_name:"Carregando..."}</span>
              <span className="pull-right-container">
                <i className="fa fa-angle-left pull-right" />
              </span>
            </a>
            <ul className="treeview-menu active">
              <li>
                <NavLink activeStyle={style} activeStyle={{
                  fontWeight: "bold",
                  color: "red"
                }} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventParticipants/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Inscritos
                  </NavLink>
              </li>
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Favorites/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Favoritos
                </NavLink>
              </li>
            </ul>
          </li>
        );
      }



    } else {
      menuStatic = (
        <li className="active treeview menu-open">
          <a href="##">
            <i className="fa fa-dashboard" />
            <span>Carregando...</span>
            <span className="pull-right-container">
              <i className="fa fa-angle-left pull-right" />
            </span>
          </a>
        </li>
      );
    }
    return (
      <div>
        <aside className="main-sidebar">
          {/* sidebar: style can be found in sidebar.less */}
          <section className="sidebar">
            {/* sidebar menu: : style can be found in sidebar.less */}
            <ul className="sidebar-menu" data-widget="tree">
              {/*MENU Event*/}
              {menuStatic}
              {/*MENU Dynamic*/}
              {this.state.CompetitionList.map(Competition => {
                if (parseInt(this.props.competitionID) === parseInt(Competition.competition_id)) {
                  return (
                    <li key={Competition.competition_id} className="treeview active">
                      <a href="##">
                        <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-dashboard" />
                        <span style={{ fontSize: `${menuSizeTop}px` }}>{" " + Competition.name}</span>
                        <span style={{ fontSize: `${menuSizeTop}px` }} className="pull-right-container">
                          <i className="fa fa-angle-left pull-right" />
                        </span>
                      </a>
                      <ul className="treeview-menu">
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/ParticipantsCompetiton/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Inscritos
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Checkpoints/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Passagens
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Winners/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Vencedores
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventGiveUp/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Não Terminaram
                          </NavLink>

                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Teams/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Equipas
                          </NavLink>
                        </li>
                      </ul>
                    </li>
                  );
                }
                else {
                  return (
                    <li key={Competition.competition_id} className="treeview">
                      <a href="##">
                        <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-dashboard" />
                        <span style={{ fontSize: `${menuSizeTop}px` }}>{"  " + Competition.name}</span>
                        <span className="pull-right-container">
                          <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-angle-left pull-right" />
                        </span>
                      </a>
                      <ul className="treeview-menu">
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/ParticipantsCompetiton/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Inscritos
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Checkpoints/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Passagens
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Winners/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Vencedores
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventGiveUp/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i className="fa fa-circle-o" /> Não Terminaram
                          </NavLink>

                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Teams/${this.props.eventID}/${Competition.competition_id}/${Competition.name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> Equipas
                          </NavLink>
                        </li>
                      </ul>
                    </li>
                  );
                }

              })}
            </ul>
          </section>

          {/* /.sidebar */}
        </aside>
      </div>
    );
  }
}