import React, { Component } from "react";
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableGiveUp from "../Tables/TableGiveUp";
import TableDisqualified from "../Tables/TableDisqualified";
import WidgetNumberParticipants from "../Widgets/NumberParticipants"
import Loader from "../Loader/Loader";
import "../../AdminLTE.css";


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
export default class EventSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      atheleGiveUPList: [],
      atheleDisqualifiedList: [],
      loadingGiveUP: false,
      loadingDisqualified: false,
    };
    this.getAthletesGiveUPList = this.getAthletesGiveUPList.bind(this);
    this.getAthletesDisqualifiedList = this.getAthletesDisqualifiedList.bind(this);
    this.getAthletesGiveUPList();
    this.getAthletesDisqualifiedList();
  }

  /*when props change*/
  componentDidUpdate(prevProps) {
    if (prevProps.match.params.CompID !== this.props.match.params.CompID) {
      this.setState({ loadingGiveUP: false, loadingDisqualified: false })
      this.getAthletesGiveUPList();
      this.getAthletesDisqualifiedList();
    }
  }

  /*Get giveup athletes*/
  getAthletesGiveUPList = async e => {

    const API_CALL = await fetch(`${API_URL}/competition/AthsGiveup/${this.props.match.params.CompID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    this.setState({ atheleGiveUPList: data, loadingGiveUP: true });

  };

  /*Get disqualified list athletes*/
  getAthletesDisqualifiedList = async e => {

    const API_CALL = await fetch(`${API_URL}/competition/AthDisqualified/${this.props.match.params.CompID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    this.setState({ atheleDisqualifiedList: data, loadingDisqualified: true });

  };


  render() {
    return (
      <div>
        <Header eventID={this.props.match.params.EventID}  />
        {/*Send the event id to the MENU, to get the competition list*/}
        <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
        {/*CONTENT*/}
        <div>
          {/* Content Wrapper. Contains page content */}
          <div className="content-wrapper">
            {/* Content Header (Page header) */}
            <section className="content-header">
              <h1>
              <span style={{float:"right"}}>{this.props.match.params.CompName}</span>
                <span>Desistiram</span>
              </h1>
            </section>
            {/* Main content */}
            {/*Check if data is loading*/}
            {(() => {
              if (this.state.loadingGiveUP === false) {
                return (
                  <section className="content">
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                            <Loader />
                          </div>

                        </div>
                      </div>
                    </div>
                  </section>
                )
              }
              else {
                return (
                  <section className="content">
                    <div className="row">
                      {/* widget for the number of participants */}
                      <WidgetNumberParticipants athleteList={this.state.atheleGiveUPList} />
                    </div>
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box-body"></div>
                          <TableGiveUp
                            eventID={this.props.match.params.EventID}
                            competitionID={this.props.match.params.CompID}
                            athleteList={this.state.atheleGiveUPList}
                            loading={this.state.loading}
                          />
                        </div>
                      </div>
                      {/* /.col */}
                    </div>
                    {/* /.row */}
                  </section>
                )
              }
            })()}
            <section className="content-header">
              <h1>
                <span style={{float:"right"}}>{this.props.match.params.CompName}</span>
                <span>Desqualificados</span>
              </h1>
            </section>
            {/*Check if data is loading*/}
            {(() => {
              if (this.state.loadingDisqualified === false) {
                return (
                  <section className="content">
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                            <Loader />
                          </div>

                        </div>
                      </div>
                    </div>
                  </section>
                )
              }
              else {
                return (
                  <section className="content">
                    <div className="row">
                      {/* widget for the number of participants */}
                      <WidgetNumberParticipants athleteList={this.state.atheleDisqualifiedList} />
                    </div>
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box-body"></div>
                          <TableDisqualified
                            eventID={this.props.match.params.EventID}
                            competitionID={this.props.match.params.CompID}
                            athleteList={this.state.atheleDisqualifiedList}
                            loading={this.state.loading}
                          />
                        </div>
                      </div>
                      {/* /.col */}
                    </div>
                    {/* /.row */}
                  </section>
                )
              }
            })()}

          </div>
          {/* /.content-wrapper */}
        </div>
        <Footer />
      </div>
    );
  }
}
